<template>
  <div class="page-box">
    <div
      v-for="(item, index) in list"
      :key="index"
      class="card"
      @click="toPage(item.url)"
    >
      {{ item.title }}
    </div>
  </div>
</template>

<script>
/* vue组件的通信方式
  props $emit
  $refs
  $children $parent
  vuex
  provide/inject
  $attrs/$listeners
  eventbus
*/
import list from './list'
export default {
  name: 'vueNote',
  data() {
    return {
      list
    }
  },
  /*beforeCreate
    created
    beforemount
    mounted
    beforeUpdate
    upDated
    beforeDestroy
    destroyed
  */
  methods: {
    toPage(url) {
      window.open(url)
    }
  }
}
</script>

<style lang="scss" scoped>
.card {
  border-radius: 8px;
  text-align: center;
  line-height: 50px;
  width: 150px;
  height: 50px;
  margin: 10px;
  box-shadow: 2px 2px 20px -10px #999999;
  cursor: pointer;
  background: #ffffff;
  transition: all 0.1s ease-in;
}
// .card + .card {
//   margin-left: 20px;
// }
.card:hover {
  box-shadow: 0 0px 20px -6px #7fb2eb;
  transform: scale(1.03);
  color: #7fb2eb;
}
.page-box {
  display: flex;
  align-content: flex-start;
  flex-wrap: wrap;
}
</style>